import { createStore } from 'vuex'

export default createStore({
  state: {
    userData:{},
    clientPortal:{},
    techPortal:{},
  },
  getters: {
    getUserData(state){
      return state.userData
    },
    getClientData(state){
      return state.clientPortal
    },
    getAppData(state){
      return state.techPortal
    }
  },
  mutations: {
    userDataMutation(state,userData){
      return state.userData = userData;
    },
    clientDataMutation(state,clientPortal){
      return state.clientPortal = clientPortal;
    },
    appDataMutation(state,techPortal){
      return state.techPortal = techPortal;
    }
  },
  actions: {
    async getAuth({commit,dispatch }){
      var myHeaders = new Headers();
      myHeaders.append("Accept", "application/json");
      myHeaders.append("Content-Type", "application/json");

      var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        redirect: 'follow',
        'x-target': 'https://oauth.bitrix.info'
      };

      let url = process.env.NODE_ENV=="development"?"proxy/":"https://oauth.bitrix.info/";
      console.log(process.env.NODE_ENV);
      await fetch(url+"oauth/token/?grant_type=refresh_token&client_id=local.6527f42b2cb1d7.48890337&client_secret=KUbOloEPwHSFPLrcfatnGk78BK2xoME4tGMIlXM4C3pKuhFABU&refresh_token=abc1516500679ed20000b79b0000014f201c077e7a174866d3ca0bae77f07bafbd4b1e", requestOptions)
        .then(response => response.text())
        .then(result => {
          const clientAuthObj = {
            "refresh_token":JSON.parse(result).refresh_token,
            "access_token":JSON.parse(result).access_token
          };
          commit("clientDataMutation",clientAuthObj);
          dispatch('getOption',JSON.parse(result));
        })
        .catch(error => console.log('error', error));
    },
    async getOption({commit},objectAuth){
      var myHeaders = new Headers();
      var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        redirect: 'follow'
      };
      fetch(`https://micros.bitrix24.ru/rest/app.option.get?auth=${objectAuth.access_token}`, requestOptions)
        .then(response => response.text())
        .then(result => {
          const data = JSON.parse(result);
          sessionStorage.client_token = JSON.stringify(data.result);
          commit("userDataMutation",data.result);
        })
        .catch(error => console.log('error', error));
    },
    async getAuthPortal({commit}){
      var myHeaders = new Headers();
      myHeaders.append("Host", "cp.micros.uz");
      
      var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        redirect: 'follow',
        'x-target': 'https://oauth.bitrix.info'
      };

      let url = process.env.NODE_ENV=="development"?"proxy/":"https://oauth.bitrix.info/";
      return fetch(url+"oauth/token/?grant_type=refresh_token&client_id=local.65142df8e8d666.01765156&client_secret=l37e9E8Ji4C0isz14qafXuvD8KTNo0WCSKyBVcCfsmJX0X5OPa&refresh_token=8c5354650067361d0004ce04000020460000079b87cb1e822a29dfc4de035dbe9220f8", requestOptions)
        .then(response => response.text())
        .then(result => {
          sessionStorage.tech_token = result;
          commit("appDataMutation",JSON.parse(result));
        })
        .catch(error => console.log('error', error));
    }
  },
  modules: {
  }
})
